import React from 'react';
import styles from './MobileVer.module.css';
import VideoPlayer from '../../Players/VideoPlayer/VideoPlayer';
import ZalupaPlayer from '../../Players/ZalupaPlayer/ZalupaPlayer';

function MobileVer({ title, date, zalupaPlayer}) {
    const { id, names, genres, description, status, type, player } = title;

    return (
        <div className={styles.container}>
            <div className={styles['title-name-specifications']}>
                <img
                    className={styles.poster}
                    src={`https://api.litelibria.com/posters/${id}.webp`}
                    alt={names.en}
                />
                <div>
                    <h2 className={styles.name}>{names.ru}</h2>
                    <div className={styles['anime-specifications']}>
                        <p className={styles.ppp}>
                            <strong className={styles['name-spec']}>Статус: </strong>
                            {status.string}
                        </p>
                        <p className={styles.ppp}>
                            <strong className={styles['name-spec']}>Жанры: </strong>
                            {genres.join(', ')}
                        </p>
                        <p className={styles.ppp}>
                            <strong className={styles['name-spec']}>Год релиза: </strong>
                            {date}
                        </p>
                        <p className={styles.ppp}>
                            <strong className={styles['name-spec']}>Тип: </strong>
                            {type.full_string}
                        </p>
                    </div>
                </div>
            </div>
            <div className={styles.player}>
                {zalupaPlayer ? (
                    <ZalupaPlayer player={player} height={'220px'} />
                ) : (
                    <VideoPlayer player={player} />
                )}
            </div>
            <p className={styles.description}>{description}</p>
        </div>
    );
}


export default MobileVer;
