import React, {useEffect, useState} from 'react';
import {useParams} from "react-router-dom";
import axios from "axios";
import loadingStyles from "../../TakeRandomAnime/TakeRandomAnime.module.css";
import Header from "../../Headers/NewHeader/NewHeader";
import ComputerVer from "../ComputerVer/ComputerVer";
import MobileVer from "../MobileVer/MobileVer";

function Mind({randomTitle}) {

    const params = useParams();


    const [title, setTitle] = useState(null);
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);


    useEffect(() => {

        if(params.code !== undefined){
            axios
                .get(`https://api.anilibria.tv/v3.0/title?code=${params.code}&remove=torrents`)
                .then((res) => setTitle(res?.data))
                .catch((err) => console.error(err))
        }
        else {
            setTitle(randomTitle);
        }

    }, [params.code, randomTitle]);

    useEffect(() => {
        const handleResize = () => {
            setIsMobile(window.innerWidth <= 768);
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);


    if (!title) {
        return <div className={loadingStyles["loading-container"]}><h2>Loading...</h2></div>;
    }


    const {season, player } = title;


    let date = "";
    if(season.string){
        date += season.string + " ";
    }
    if(season.year){
        date += season.year;
    }


    let zalupaPlayer = false;
    if(Object.keys(player.list).length === 0){
        zalupaPlayer = true;
    }

    return (
        <div>
            <Header isTitle={true} />
            {isMobile ? (
                <div className="mobile-version">
                    <MobileVer title={title} date={date} zalupaPlayer={zalupaPlayer}/>
                </div>
            ) : (
                <div className="computer-version">
                    <ComputerVer title={title} date={date} zalupaPlayer={zalupaPlayer} />
                </div>
            )}
        </div>
    );
}

export default Mind;
