import React, {useState, useEffect} from 'react';
import styles from "./Filter.module.css";
import Select from "react-select";
import axios from "axios";


function FilterMenu({filterURL}){
    const [genres, setGenres] = useState([]);
    const [years, setYears] = useState([]);

    const [selectedGenres, setSelectedGenres] = useState([]);
    const [selectedYears, setSelectedYears] = useState([]);
    const [selectedTypes, setSelectedTypes] = useState([]);


    useEffect(() => {
        const fetchGenres = async () => {
            try {
                const response = await axios.get('https://api.anilibria.tv/v3.0/genres');
                setGenres(response.data);
            } catch (error) {
                console.error('Failed to fetch genres:', error);
            }
        };

        fetchGenres();
    }, []);

    useEffect(() => {
        const fetchYears = async () => {
            try {
                const response = await axios.get('https://api.anilibria.tv/v3.0/years');
                setYears(response.data);
            } catch (error) {
                console.error('Failed to fetch years:', error);
            }
        };

        fetchYears();
    }, []);

    const handleSearch = async () => {
        let url = 'https://api.anilibria.tv/v3.0/title/search?';

        if (selectedGenres.length > 0) {
            const genreString = selectedGenres.map(genre => genre.value).join(',');
            url += `genres=${genreString}&`;
        }

        if (selectedYears.length > 0) {
            const yearString = selectedYears.map(year => year.value).join(',');
            url += `year=${yearString}&`;
        }

        if (selectedTypes.length > 0) {
            const typeString = selectedTypes.map(type => type.value).join(',');
            url += `type=${typeString}&`;
        }

        filterURL(url);
    };


    const customStyles = {
        control: (provided, state) => ({
            ...provided,
            backgroundColor: '#262626',
            color: '#FFFFFF',
            border: state.isFocused ? '2px solid #1d1d1d' : '2px solid #1d1d1d',
            boxShadow: state.isFocused ? 'none' : 'none',
            '&:hover': {
                border: state.isFocused ? '2px solid #1d1d1d' : '2px solid #1d1d1d'
            }
        }),
        option: (provided, state) => ({
            ...provided,
            backgroundColor: state.isFocused ? '#1d1d1d' : '#262626',
            color: '#FFFFFF',
            '&:hover': {
                backgroundColor: '#1d1d1d'
            }
        }),
        multiValue: (provided) => ({
            ...provided,
            backgroundColor: '#1d1d1d',
        }),
        multiValueLabel: (styles, { data }) => ({
            ...styles,
            color: data.color,
        }),
        indicatorSeparator: () => ({
            display: 'none'
        }),
        menu: (provided, state) => ({
            ...provided,
            backgroundColor: '#262626',
            marginTop: '2px',
            borderRadius: '4px'
        })
    };

    return(
        <div className={styles["selector-row"]}>
            <Select
                placeholder="Жанры"
                className={styles.selector}
                isMulti
                name="title-genre"
                classNamePrefix="select"
                styles={customStyles}
                isSearchable={false}
                closeMenuOnSelect={false}
                options={genres.map(genre => ({ value: genre, label: genre }))}
                onChange={setSelectedGenres}
            />
            <Select
                placeholder="Год выхода"
                className={styles.selector}
                isMulti
                name="title-year"
                classNamePrefix="select"
                styles={customStyles}
                isSearchable={false}
                closeMenuOnSelect={false}
                options={years.map(year => ({ value: year, label: year.toString() }))}
                onChange={setSelectedYears}
            />
            <Select
                placeholder="Тип"
                className={styles.selector}
                isMulti
                name="title-type"
                classNamePrefix="select"
                styles={customStyles}
                isSearchable={false}
                closeMenuOnSelect={false}
                options={[
                    { value: 0, label: "Фильм" },
                    { value: 1, label: "TV" },
                    { value: 2, label: "OVA" },
                    { value: 3, label: "ONA" },
                    { value: 4, label: "Спешил" },
                    { value: 5, label: "WEB" },
                ]}
                onChange={setSelectedTypes}
            />
            <button className={styles["search-btn"]} onClick={handleSearch}>Поиск</button>
        </div>
    );
}

export default FilterMenu;